.math-page {
  padding-top: 100px;
    align-items: center;
    justify-content: center;
  }

  .math-content {
    margin-left:auto;
    margin-right: auto;
    border: 10px white;
    display: flex;
    flex-direction: column;
    padding-bottom: 100px;
    width: fit-content;
  }

  .math-header {
    font-size: 40px;
    color: white;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
  }

  .galois-pic {
    width: 900px;
    margin-bottom: 30px; 
    margin-left: auto;
    margin-right: auto;
    border-radius: 20px; /* Add rounded edges */
  }

  @media (max-width: 1200px) {
    .galois-pic {
      width: 90vw;
    }
    .math-content {
      width: 90vw;
    }
  }